'use strict';

import mongoose from 'mongoose';

var SettingSchema = new mongoose.Schema({
  globalVAT: {
  	type:Number
  },
  totalTables:{
  	type:Number
  },
  loyalityPercentage:{
    type:Number,
    default:0
  },
  minLoyalityPoints:{
    type:Number,
    default:0
  },
  loyalityProgram:{
    type:Boolean,
    default:true
  },
  //min order value on which loyality calculated
  minOrdLoyality:{
    type:Number
  },
  createdAt: {
    type: Date,
    default: Date.now
 },
  updatedAt: { 
    type: Date 
 }
});

export default mongoose.model('Setting', SettingSchema);
